<template>
    <div class="common-layout">
      <el-container direction="horizontal">
        <el-aside width="500px">
          <div id="app">
            <h1>My To-Do List</h1>
            <to-do-form @todo-added="addToDo"></to-do-form>
            <h2 id="list-summary" ref="listSummary" tabindex="-1">{{listSummary}}</h2>
            <ul aria-labelledby="list-summary" class="stack-large">
              <li v-for="item in ToDoItems" :key="item.id">
                <to-do-item
                  :label="item.label"
                  :done="item.done"
                  :id="item.id"
                  :deadline="item.deadline"
                  @checkbox-changed="updateDoneStatus(item.id)"
                  @item-deleted="deleteToDo(item.id)"
                  @item-edited="editToDo(item.id, $event.label, $event.deadline)">
                </to-do-item>
              </li>
            </ul>
          </div>
        </el-aside>
        </el-container>
        <el-container>
          <div id="main" style="width: 650px;height: 600px;"></div>
        </el-container>
  </div>
  
</template>

<script>
import ToDoItem from "./components/ToDoItem.vue";
import uniqueId from "lodash.uniqueid";
import ToDoForm from "./components/ToDoForm.vue";
import * as echarts from 'echarts';

export default {
  name: "app",
  components: {
    ToDoItem, ToDoForm,
  },
  data() {
    return {
      ToDoItems: [
        { id: uniqueId("todo-"), label: "Learn Vue", done: false, deadline: "2024-12-05"},
        {
          id: uniqueId("todo-"),
          label: "Create a Vue project with the CLI",
          done: true,
          deadline: "2024-12-10",
        },
        { id: uniqueId("todo-"), label: "Have fun", done: true, deadline: "2024-12-15" },
        { id: uniqueId("todo-"), label: "Create a to-do list", done: false, deadline: "2024-12-20" },
      ],
    };
  },
  methods: {
    addToDo(toDoLabel) {
      this.ToDoItems.push({
        id: uniqueId("todo-"),
        label: toDoLabel,
        done: false,
        deadline: new Date().toISOString().split('T')[0], // 默认的截止日期为添加当天的日期
      });
    },
    updateDoneStatus(toDoId) {
      const toDoToUpdate = this.ToDoItems.find((item) => item.id === toDoId);
      toDoToUpdate.done = !toDoToUpdate.done;
    },
    deleteToDo(toDoId) {
      const itemIndex = this.ToDoItems.findIndex((item) => item.id === toDoId);
      this.ToDoItems.splice(itemIndex, 1);
    },
    editToDo(toDoId, newLabel, newDeadline) {
      const toDoToEdit = this.ToDoItems.find((item) => item.id === toDoId);
      toDoToEdit.label = newLabel;
      toDoToEdit.deadline = newDeadline;
    },
    updateChartData() {
      this.$emit('update-chart-data', this.chartData); // 发出自定义事件
    },
    updateChart(newData) {
      const chartDom = document.getElementById('main');
      const myChart = echarts.init(chartDom);
      const option = {
        title: {
          text: '待办事项的沙漏图（重要性越高，剩余时间越长）'
        },
        tooltip: {
          trigger: 'item',
          formatter: '{a} <br/>{b} : {c}%'
        },
        toolbox: {
          feature: {
            dataView: { readOnly: false },
            restore: {},
            saveAsImage: {}
          }
        },
        series: [{
          name: 'Funnel',
          type: 'funnel',
          left: '10%',
          top: 60,
          bottom: 60,
          width: '80%',
          min: 0,
          max: 100,
          minSize: '0%',
          maxSize: '100%',
          sort: 'descending',
          gap: 2,
          label: {
            show: true,
            position: 'inside'
          },
          labelLine: {
            length: 10,
            lineStyle: {
              width: 1,
              type: 'solid'
            }
          },
          itemStyle: {
            borderColor: '#fff',
            borderWidth: 1
          },
          emphasis: {
            label: {
              fontSize: 20
            }
          },
          data: newData
        }]
      };
      myChart.setOption(option);
    }
  },
  computed: {
    listSummary() {
      const numberFinishedItems = this.ToDoItems.filter((item) => item.done).length;
      return `${numberFinishedItems} out of ${this.ToDoItems.length} items completed`;
    },

    chartData() {
  if (this.ToDoItems.length === 0) return []; 
  const now = new Date(); 
  const sortedItems = [...this.ToDoItems].sort((a, b) => {
    const deadlineA = new Date(a.deadline);
    const deadlineB = new Date(b.deadline);
    return (deadlineA.getTime() - now.getTime()) - (deadlineB.getTime() - now.getTime()); 
  });
  const totalItems = sortedItems.length;
  const importancePerItem = 100 / totalItems;  
  const chartData = sortedItems.map((item, index) => ({
    value: (index + 1) * importancePerItem, 
    name: item.label
  }));

  return chartData;
}
  },
 watch: {
    chartData: {
      handler(newData) {
        this.updateChart(newData);
      },
      deep: true,
    }
  },
  mounted() {
    this.updateChart(this.chartData);
  },
};
</script>

